<template>
  <div class="contentWrapper">
    <h4 class="contentTitle">我的购物车：</h4>

    <fieldset>
      <!-- checkout product starts -->
      <div class="checkoutProductWrapper" v-for="(item,index) in cart.items" :key="index">
        <a href="#" class="checkoutProductImageWrapper">
          <img :src="'images/content/'+item.id+'-2.jpg'" class="checkoutProductImage" alt />
        </a>
        <div class="checkoutProductInfoWrapper">
          <a href="#" class="checkoutProductTitle">{{item.name}}</a>
          <div class="checkoutProductButtonsWrapper">
            <input
              type="text"
              class="checkoutProductNumber checkoutProductNumber-1"
              name="product-1"
              :value="item.quantity"
              @keyup.enter="update(item.id,$event)"
              @blur="update(item.id,$event)"
            />
            <span class="checkoutProductPrice">￥ {{item.unitPrice}}</span>
            <a href="#" class="checkoutRemoveProductButton" @click="del(item.id)"></a>
          </div>
        </div>
      </div>
      <!-- checkout product ends -->

      <!-- checkout info wrapper starts -->
      <div class="checkoutInfoWrapper">
        <span class="checkoutProductsNumber">商品数量:{{allQuantity}}</span>
        <span class="checkoutProductsTotal">总价格: ${{allPrice}}</span>
        <router-link to="/orders">dingdan</router-link>
      </div>
      <!-- checkout info wrapper ends -->
      
      <div class="checkoutButtonsWrapper" v-show="isLogin">
        <input
          type="submit"
          value="确认购买"
          @click="addOrder"
          id="checkoutCheckoutButton"
          class="checkoutCheckoutButton"
        />
      </div>
    </fieldset>
  </div>
</template>

<script>
import CartService from "@/service/CartService";
import UserService from "@/service/UserService";
export default {
  name: "Cart",
  data() {
    return {
      cart: {},
      isLogin: false,
    };
  },
  computed: {
    // 计算总数量
    allQuantity() {
      return this.cart.totalQuantity();
    },
    // 计算总价格
    allPrice() {
      return this.cart.totalPrice();
    },
  },
  created() {
    // 实例化购物车
    this.cart = new CartService();
    // 加载购物车数据
    this.cart.loadCart();
    // 设置isLogin状态
    let userService = new UserService();
    // 如果sessionStorage 中 user 不为空
    if (userService.loadUser()) {
      this.isLogin = true;
    }
  },
  methods: {
    // 删除商品
    del(id) {
      // 根据id删除商品
      this.cart.removeCart(id);
      // 保存购物车
      this.cart.saveCart();
    },
    // 修改商品数量
    update(id, e) {
      let quantity = e.target.value;
      // 修改商品数量
      this.cart.updateQuantity(id, quantity);
      // 保存购物车
      this.cart.saveCart();
    },
    // 添加订单
    async addOrder() {
      if (this.cart.items.length < 1) return ;
      let details = [];
      this.cart.items.forEach((x) => {
        let item = {
          product: { id: x.id, unitPrice: x.unitPrice },
          quantity: x.quantity,
        };
        details.push(item);
      });
      // 获取token
      let userService = new UserService();
      let token = userService.loadUser().token;
      // 插入订单
      const { data: res } = await axios.post(
        "/api/order/add",
        { details: details },
        {
          headers: {
            Authorization: "auth_" + token,
          },
        }
      );
      console.log(res)
      this.cart.clearCart();
      this.$router.push("/");
    },
  },
};
</script>

