<template>
  <div>
    <div v-for="item in orders" :key="item.id">
      <h5 @click="showDetail(item.id)">{{item.consignee}}</h5>
      <h5>总金额：{{item.totalPrice}}</h5>
      <h5>时间：{{item.orderTime}}</h5>
      <h5>phone：{{item.phone}}</h5>
      <h5>时间：{{item.orderTime}}</h5>
      <br>
      <div class="detail" v-for="p in products" :key="p.name">
        <span>名称:{{p.product.name}}</span>&nbsp;&nbsp;&nbsp;&nbsp;
        <span>单价:{{p.product.unitPrice}}</span>&nbsp;&nbsp;&nbsp;&nbsp;
        <span>数量:{{p.quantity}}</span>
      </div>
      <hr>
    </div>
  </div>
</template>

<script>
import UserService from "../service/UserService";
export default {
  data() {
    return {
      orders: {},
      products: {},
    };
  },
  computed: {
    myDate() {
      var date = new Date(times);
      var result =
        date.getFullYear + "-" + (date.getMonth + 1) + "-" + date.getDate;
      return result;
    },
  },
  created() {
    let userService = new UserService();
    let username = userService.loadUser().username;
    console.log(username);
    axios.get("/api/order/find?username=" + username).then((res) => {
      this.orders = res.data.data;
    });
  },
  methods: {
    showDetail(id) {
      axios.get("/api/order/details/" + id).then((res) => {
          console.log(res.data.data)
        this.products = res.data.data;
      });
    },
  },
};
</script>

