import Vue from 'vue'
import VueRouter from 'vue-router'
// import ProductList from '../components/ProductList'
import LoginPart from '@/views/LoginPart'
// import Home from '@/components/Home'
import Cart from '@/components/Cart'
import ProductDetail from '@/components/ProductDetail'

// import RegisterPart from '../components/RegisterPart'
import Orders from "../components/Orders"

Vue.use(VueRouter)

const routes = [
  {
    path: '/login',
    name: 'Login',
    component: LoginPart
  },{
    path: '/register',
    name:'Register',
    component:()=>import('@/views/Register.vue')
  },
  {
    path: '/',
    component: ()=>import('@/views/Index.vue'),
    children:[
      {path: '',name:'admin',component:()=>import('@/components/Home.vue')},
      {path: '/product/:id',name:'product',component:()=>import('@/components/ProductDetail.vue')},
    ]
  },
  {
    path: '/product/:id',
    name: 'Product',
    component: ProductDetail
  },
  {
    path: '/cart',
    name: 'Cart',
    component: Cart
  },
  // {
  //   path: '/list',
  //   name: 'List',
  //   component: ProductList
  // },
  {
    path: "/orders",
    name: 'Orders',
    component: Orders
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router

const originalPush = VueRouter.prototype.push
   VueRouter.prototype.push = function push(location) {
   return originalPush.call(this, location).catch(err => err)
}
